@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Navbar {
    width: 100%;
    padding: 0.5rem 0;
    background-color: #1C3643;
  }
  
  .Navbar__brand {
    color: #ffffff;
  
    display: flex;
    align-items: center;
  }
  
  .Navbar__brand:hover {
    color: #ffffff;
    text-decoration: none;
  }
  
  .Navbar__brand-logo {
    margin-right: 0.5rem;
    max-width: 10%;
    max-height: 10%;
  }
.Home {
    width: 100%;
    min-height: calc(100vh - 60px);
    background: url(/static/media/stars.1d17360a.svg), #1B1B25;
    background-repeat: repeat;
  
    color: #ffffff;
  
    display: flex;
    align-items: center;
  }
  
  .Home__col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .Home_signup{
    /* margin: 1px solid red; */
    margin-top: 0.5rem;
  }
  @media screen and (min-width: 768px) {
    .Home__col {
      align-items: flex-start;
    }
  }
.lds-grid {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
  }
  
  .lds-grid div {
    position: absolute;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #000;
    -webkit-animation: lds-grid 1.2s linear infinite;
            animation: lds-grid 1.2s linear infinite;
  }
  
  .lds-grid div:nth-child(1) {
    top: 6px;
    left: 6px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
  }
  
  .lds-grid div:nth-child(2) {
    top: 6px;
    left: 26px;
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
  }
  
  .lds-grid div:nth-child(3) {
    top: 6px;
    left: 45px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
  
  .lds-grid div:nth-child(4) {
    top: 26px;
    left: 6px;
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
  }
  
  .lds-grid div:nth-child(5) {
    top: 26px;
    left: 26px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
  
  .lds-grid div:nth-child(6) {
    top: 26px;
    left: 45px;
    -webkit-animation-delay: -1.2s;
            animation-delay: -1.2s;
  }
  
  .lds-grid div:nth-child(7) {
    top: 45px;
    left: 6px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
  
  .lds-grid div:nth-child(8) {
    top: 45px;
    left: 26px;
    -webkit-animation-delay: -1.2s;
            animation-delay: -1.2s;
  }
  
  .lds-grid div:nth-child(9) {
    top: 45px;
    left: 45px;
    -webkit-animation-delay: -1.6s;
            animation-delay: -1.6s;
  }
  
  @-webkit-keyframes lds-grid {
  
    0%,
    100% {
      opacity: 1;
    }
  
    50% {
      opacity: 0.5;
    }
  }
  
  @keyframes lds-grid {
  
    0%,
    100% {
      opacity: 1;
    }
  
    50% {
      opacity: 0.5;
    }
  }
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}
